.sidebar {
	height: 100vh;
	width: 200px;
	background: #001529;
	transition: all .4s;
	@media (max-width: 1050px) {
		position: fixed;
		right: 0px;
		top: 0;
		z-index: 9999999;
	}
}

.sidebarMedia {
	@media (max-width: 1050px) {
		margin-top: 3.65em !important;
	}
}

.closedsidebar {
	height: 110vh;
	width: 80px;
	background: #001529;
	transition: all .4s;
	@media (max-width: 1050px) {
		width: 0px;
		right: 0px;
		top: 0;
		position: fixed;
		z-index: 9999999;
	}
}
