.back {
	border-radius: 4px;
	width: 100%;
	color: white;
	-webkit-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	-moz-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	transition: all .4s;
	&:hover {
		-webkit-box-shadow: 0px 0px 19px 0px rgba(0, 0, 0, 0.75);
		-moz-box-shadow: 0px 0px 19px 0px rgba(0, 0, 0, 0.75);
		box-shadow: 0px 0px 19px 0px rgba(0, 0, 0, 0.75);
	}
}

.green {
	background: rgb(2, 0, 36);
	background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(19, 185, 71, 1) 0%, rgba(0, 227, 164, 1) 100%);
}

.blue {
	background: rgb(2, 0, 36);
	background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(0, 198, 255, 1) 0%, rgba(0, 56, 255, 1) 100%);
}

.orange {
	background: rgb(2, 0, 36);
	background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(255, 115, 0, 1) 0%, rgba(255, 205, 0, 1) 100%);
}

.red {
	background: rgb(2, 0, 36);
	background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(255, 0, 69, 1) 0%, rgba(255, 0, 202, 1) 100%);
}

.cart {
	width: 35px;
}

.percentageImg {
	width: 24px;
	padding-left: .4em;
}
